@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&family=Poppins:ital,wght@0,500;1,500&display=swap);
.App {
    font-family: 'Noto Sans KR', sans-serif;



}

.Home {
    text-align: center;
  }
  
  
  .Home-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  .Home-link {
    color: #61dafb;
  }
  
  .Home .homepageWelcome {
    text-align: left;
    padding: 100px 0;
    padding-left: 75px;
  }

  .Home .homepageWelcome h1{
    font-size: 100px;
  }
    
  #background_img {
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    min-height: 600px;
    height: calc(100vh);
    background-position:right top;
  }

  .yellow-font {
    /* Yellow from Wix site color: #D6CC11; */
    color: #f4d160;
  }

  .title {
    color: #f4d160;
  }

  .title:hover {
    color: #f8dc80;
    cursor: pointer;
  }

  
  .blue-background {
    /* Blue from Wix site background-color: #093F7C; */
    background-color: #28527a;
  }

  .yellow-background {
    background-color: #f4d160; 
  }

  .center-align {
    text-align: center;
  }

  .buttons button, a.btn {
    margin: 10px 0px;;
    background-color: #315e92;
    color: #c4dbf0;
    text-decoration: none;
  }

  .buttons button:hover {
    background-color: #28527a;
    color: #f4d160;
    text-decoration: none;
  }

  .buttons a:hover {
    background-color: #28527a;
    color: #f4d160;
    text-decoration: none;
  }

.profile_pic {
  height: 60px;
  width: 60px;
  margin-bottom: 10px;
  object-fit: cover;
}

.blue-font {
  color: #28527a;
}

.toggle button {
  margin: 10px 0px;;
  background-color: #315e92;
  color: #f4d160;
  text-decoration: none;
}

.toggle button:hover {
  background-color: #28527a;
  color: #f4d160;
  text-decoration: none;
}

.freelancer_card {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    max-width: 300px;
    margin: 25px 5px;
    text-align: center;
    padding: 10px;
    width: 100%;
    background-color: #315e92!important;
  }

.light-blue-bg {
  background-color: #315e92!important;
}

.freelancer_card button {
  border: none;
  outline: 0;
  padding: 12px;
  color: #f4d160;
  background-color: #28527a;
  text-align: center;
  cursor: pointer;
  width: 100%;
  font-size: 18px;
}

.freelancer_card button:hover {
  background-color: #28527acc;
  text-decoration: none;
  color: #f4d160c4;
}

.card_image {
    height: 125px;
    width: 100%;
    object-fit: cover;

}

.freelancer_header {
  text-align: center;
  padding: 20px;
}

.card_btn {
  cursor: pointer;
  font-weight: 9000;
}

.card_btn:hover {
  color: #f4d160bb;

}

.footer {
  padding: 20px;
}

.Portfolio {
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    height: 100%;
    width: 100%;
    background-position:right top;
    background-color: #28527a!important;
}

.Portfolio h1{
  font-size: 50px;
  text-align: centre;
  color: #315e92!important;
}

.Portfolio img {
  max-height: 500px;
  width: 100%;
  border: 5px solid #28527a;
  border-radius: 2px;
}

.carousel{
  width:80%;
  height:500px;
  margin: auto;
}

.Portfolio .main_carousel img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  border: 5px solid #315e92!important;
  border-radius: 2px;
}

.biography {
  background-color: #315e92!important;
  border-radius: 2px;
  width: 80%;
  align-items: center;
  margin: auto;
  padding-bottom: 10px;
  padding-left: 10px;
  padding-right: 10px;
}

.biography h2{
  font-size: 20px;
  color: #f4d160;
  font-weight: bold;
}

.biography h3{
  font-size: 20px;
  color: #f4d160;
}

.gallery img{
  width: 25%;
  height: 100%;
  object-fit: cover;
}

.contact-btn button {
  border: none;
  outline: 0;
  padding: 12px;
  color: #f4d160;
  background-color: #28527a;
  text-align: center;
